.shake-horizontal {
  animation: shake-horizontal 4s cubic-bezier(.455, .03, .515, .955) infinite both;
}

@keyframes shake-horizontal {
  0%, 100% {
    transform: translateX(0);
  }

  10% {
    transform: translateX(-10px);
  }

  20% {
    transform: translateX(-10px);
  }

  50% {
    transform: translateX(-10px);
  }

  60% {
    transform: translateX(10px);
  }

  70% {
    transform: translateX(-10px);
  }

  80% {
    transform: translateX(8px);
  }

  90% {
    transform: translateX(-8px);
  }
}

@keyframes elipse-scale {
  50% {
    transform: scale(.7);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes icecream-twik {
  0% {
    transform: translateX(200px);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes icecream-scale {
  0% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1);
  }
}

.backdrop {
